export default {
  // 
  namespaced: true,

  //  state 
  state: () => ({
    postList: []
  }),

  //  mutations 
  mutations: {
    AssignPostList(state, array) {
      state.postList = array
    }
  },

  //  getters 
  getters: {},
  actions: {
    async getPostList(context) {
      const res = await uni.$http.get('/api/resources')
      context.commit("AssignPostList", res.data)
    }
  }
}